/* eslint-disable */
<template>
  <div>
    <div id="buttons">
      <button v-on:click="reset">Reset</button>
      <button v-on:click="pathFind">Path find</button>
    </div>
    <p>{{ errorText }}</p>
    <canvas id="board" width="750" height="750"></canvas>
  </div>
</template>

<script>
export default {
  name: "Board",
  props: ["mode"],
  data() {
    return {
      flagStart: false,
      flagFinish: false,
      x: 0,
      y: 0,
      width: 750,
      S: -1,
      F: -1,
      errorText: "",
      path: []
    };
  },
  computed: {
    n: function() {
      return parseInt(this.$vssWidth) < 1080 ? 15 : 20;
    },
    V: function() {
      return this.n * this.n;
    },
    field: function() {
      var field = new Array(this.n);
      for (let i = 0; i < this.n; i++) {
        field[i] = new Array(this.n);
      }
      for (let i = 0; i < this.n; i++) {
        for (let j = 0; j < this.n; j++) {
          field[i][j] = 0;
        }
      }
      return field;
    },
    adja: function() {
      var adja = new Array(this.V);
      for (let i = 0; i < this.V; i++) {
        adja[i] = [];
      }
      return adja;
    },
    parent: function() {
      return new Array(this.V);
    },
    dist: function() {
      return new Array(this.V);
    },
    step: function() {
      return this.width / this.n;
    }
  },
  methods: {
    h: function(n) {
      let posF = this.numberToPosition(this.F);
      let posN = this.numberToPosition(n);
      let dx = posF[0] - posN[0];
      let dy = posF[1] - posN[1];
      return Math.sqrt(dx * dx + dy * dy);
    },
    relax: function(v, w, Q) {
      if (this.dist[w] > this.dist[v] + 1) {
        this.parent[w] = v;
        this.dist[w] = this.dist[v] + 1;
        if (Q.contains(w)) {
          Q.remove(w);
        }
        Q.enqueue(w, this.dist[w] + this.h(w));
      }
    },
    findPos: function(x, y) {
      return [Math.floor(x / this.step), Math.floor(y / this.step)];
    },
    numberToPosition: function(num) {
      var pos = [];
      pos[0] = Math.floor(num / this.n);
      pos[1] = num % this.n;
      return pos;
    },
    positionToNumber: function(x, y, n = this.n) {
      return x * n + y;
    },
    get: function(x, y) {
      return this.field[x][y];
    },
    set: function(x, y, value) {
      this.field[x][y] = value;
    },
    setStart: function(x, y) {
      this.set(x, y, "s");
      this.S = this.positionToNumber(x, y);
    },
    setFinish: function(x, y) {
      this.set(x, y, "f");
      this.F = this.positionToNumber(x, y);
    },
    addEdge: function(v, w) {
      this.adja[v].push(w);
      this.adja[w].push(v);
    },
    adj: function(v) {
      return this.adja[v];
    },
    printBoard: function() {
      console.log(this.field);
    },
    build: function() {
      for (let i = 0; i < this.V; i++) {
        if (i - this.n >= 0 && this.adja[i].indexOf(i - this.n) == -1)
          this.addEdge(i, i - this.n);
        if (
          i + this.n >= 0 &&
          i + this.n < this.V &&
          this.adja[i].indexOf(i + this.n) == -1
        )
          this.addEdge(i, i + this.n);
        if (this.adja[i].indexOf(i + 1) == -1) {
          if (i % this.n != this.n - 1) {
            this.addEdge(i, i + 1);
          }
        }
        if (this.adja[i].indexOf(i - 1) == -1) {
          if (i % this.n != 0) {
            this.addEdge(i, i - 1);
          }
        }
      }
    },
    reset: function() {
      for (let i = 0; i < this.n; i++) {
        for (let j = 0; j < this.n; j++) {
          this.field[i][j] = 0;
        }
      }
      this.S = -1;
      this.F = -1;
      this.errorText = "";
      this.vueCanvas.clearRect(0, 0, this.width, this.width);
      this.drawGrid(this.vueCanvas);
    },
    drawGrid: function(c) {
      for (let x = 0; x <= this.width; x += this.step) {
        c.moveTo(x, 0);
        c.lineTo(x, this.width);
        c.stroke();
      }
      for (let y = 0; y <= this.width; y += this.step) {
        c.moveTo(0, y);
        c.lineTo(this.width, y);
        c.stroke();
      }
    },
    drawPath: function(path) {
      for (let i = 0; i < path.length; i++) {
        let pos = this.numberToPosition(path[i]);
        this.vueCanvas.fillStyle = "blue";
        this.vueCanvas.fillRect(
          pos[0] * this.step,
          pos[1] * this.step,
          this.step,
          this.step
        );
      }
    },
    drawBlack: function(pos) {
      var num = this.positionToNumber(pos[0], pos[1]);
      if (num == this.S || num == this.F) return;
      this.vueCanvas.fillStyle = "black";
      this.vueCanvas.fillRect(
        pos[0] * this.step,
        pos[1] * this.step,
        this.step,
        this.step
      );
      this.set(pos[0], pos[1], 1);
    },
    addWalls: function(e, c) {
      this.x = e.pageX - c.offsetLeft;
      this.y = e.pageY - c.offsetTop;
      let pos = this.findPos(this.x, this.y);
      this.drawBlack(pos);
    },
    initCanvas: function() {
      var c = document.getElementById("board");
      var ctx = c.getContext("2d");
      this.vueCanvas = ctx;
      this.drawGrid(ctx);
      var that = this;
      c.addEventListener("click", function(e) {
        that.x = e.pageX - c.offsetLeft;
        that.y = e.pageY - c.offsetTop;
        var pos = [
          Math.floor(that.x / that.step) * that.step,
          Math.floor(that.y / that.step) * that.step
        ];
        switch (that.mode) {
          case 0:
            var coor = that.findPos(that.x, that.y);
            var num = that.positionToNumber(coor[0], coor[1]);
            if (num != that.S && num != that.F) {
              ctx.fillStyle = "white";
              ctx.fillRect(pos[0], pos[1], that.step, that.step);
              that.set(coor[0], coor[1], 0);
            }
            break;
          case 2:
            if (that.S < 0) {
              ctx.fillStyle = "green";
              ctx.fillRect(pos[0], pos[1], that.step, that.step);
              let coor = that.findPos(that.x, that.y);
              that.setStart(coor[0], coor[1]);
            }
            break;
          case 3:
            if (that.F < 0) {
              ctx.fillStyle = "orange";
              ctx.fillRect(pos[0], pos[1], that.step, that.step);
              let coor = that.findPos(that.x, that.y);
              that.setFinish(coor[0], coor[1]);
            }
            break;
          default:
            break;
        }
        that.drawGrid(ctx);
      });
      var isClicked = false;
      c.addEventListener("mousedown", function(e) {
        isClicked = true;
        if (that.mode == 1) {
          that.addWalls(e, c);
        }
      });
      c.addEventListener("mousemove", function(e) {
        if (that.mode == 1 && isClicked) {
          that.addWalls(e, c);
        }
      });
      c.addEventListener("mouseup", function() {
        isClicked = false;
      });
      c.addEventListener("touchstart", function(e) {
        isClicked = true;
        if (that.mode == 1) {
          that.addWalls(e, c);
        }
      });
      c.addEventListener("touchmove", function(e) {
        if (that.mode == 1 && isClicked) {
          that.addWalls(e, c);
        }
      });
      c.addEventListener("touchend", function() {
        isClicked = false;
      });
    },
    resetPathFind: function() {
      this.parent.fill(-1);
      this.dist.fill(Infinity);
      let that = this;
      this.path.forEach(function(e) {
        let pos = that.numberToPosition(e);
        if (that.field[pos[0]][pos[1]] == 1) return;
        that.vueCanvas.clearRect(
          pos[0] * that.step,
          pos[1] * that.step,
          that.step,
          that.step
        );
      });
      this.drawGrid(this.vueCanvas);
      this.path = [];
      this.errorText = "";
    },
    color: function(color, n) {
      let pos = this.numberToPosition(n);
      this.vueCanvas.fillStyle = '#' + color;
      this.vueCanvas.fillRect(
        pos[0] * this.step,
        pos[1] * this.step,
        this.step,
        this.step
      );
    },
    pathFind: function() {
      class PQElem {
        constructor(elem, prio) {
          this.elem = elem;
          this.prio = prio;
        }
      }
      class PQ {
        constructor() {
          this.queue = [];
        }
        enqueue(elem, prio) {
          var Element = new PQElem(elem, prio);
          var contains = false;
          for (let i = 0; i < this.queue.length; i++) {
            if (this.queue[i].prio > Element.prio) {
              this.queue.splice(i, 0, Element);
              contains = true;
              break;
            }
          }
          if (!contains) this.queue.push(Element);
        }
        dequeue() {
          return this.queue.shift();
        }
        isEmpty() {
          return this.queue.length == 0;
        }
        contains(e) {
          for (let i = 0; i < this.queue.length; i++) {
            if (this.queue[i].elem == e) return true;
          }
          return false;
        }
        remove(e) {
          for (let i = 0; i < this.queue.length; i++) {
            if (this.queue[i].e == e) this.queue.splice(i, 1);
            break;
          }
        }
      }
      if (this.S < 0) {
        this.S = 0;
        this.set(0, 0, "s");
        this.vueCanvas.fillStyle = "green";
        this.vueCanvas.fillRect(0, 0, this.step, this.step);
      }
      if(this.F < 0) {
        this.F = this.V - 1;
        this.set(this.n - 1, this.n - 1, "f");
        this.vueCanvas.fillStyle = "orange";
        this.vueCanvas.fillRect(
          (this.n - 1) * this.step,
          (this.n - 1) * this.step,
          this.step,
          this.step
        );
      }
      var Q = new PQ();
      this.resetPathFind();
      this.dist[this.S] = 0;
      Q.enqueue(this.S, this.h(this.S));
      while (!Q.isEmpty()) {
        let v = Q.dequeue();
        v = v.elem;
        if (v == this.F) break;
        var adj = this.adj(v);
        for (let i = 0; i < adj.length; i++) {
          let w = adj[i];
          let posW = this.numberToPosition(w);
          if (this.get(posW[0], posW[1]) != 1) this.relax(v, w, Q);
        }
      }
      var tmp = this.parent[this.F];
      while (tmp != -1 && tmp != this.S) {
        this.path.unshift(tmp);
        tmp = this.parent[tmp];
      }
      if (tmp != this.S) {
        this.errorText = "NO PATH FOUND!";
        return;
      }
      this.drawPath(this.path);
    }
  },
  mounted() {
    this.build();
    this.initCanvas();
  }
};
</script>

<style scoped>
#buttons {
  width: 750px;
  margin: auto;
  display: flex;
  justify-content: space-around;
}
#buttons button {
  background-color: red;
  color: white;
  width: 20%;
  height: 35px;
  font-size: 25px;
}
#board {
  background-color: white;
  margin: auto;
  margin-top: 30px;
}
p {
  color: red;
  font-weight: bold;
}
</style>
